<template>
  <user>
    <html-head
      v-if="role.data.name"
      :title="role.data.name"
    ></html-head>

    <page-header
      :to="{ name: 'roles.index' }"
    >
      <template v-slot:title >
        <span v-text="`Role Information`"></span>
      </template>
    </page-header>

    <v-row>
      <v-col cols="12" sm="8" lg="6" class="mx-auto">
        <show-skeleton v-if="role.isFetching"></show-skeleton>
        <v-card v-else elevation="0">
          <v-card-title v-text="`${ role.data.name }`"></v-card-title>
          <v-card-text>
            <v-treeview
              :items="role.data.categorizedPermissions"
              :value="activePermissions"
              item-key="_id"
              multiple-active
              disabled
            >
              <template v-slot:label="{ item }">
                <template v-if="item.name">
                  <span v-text="item.name"></span>
                </template>
                <template v-else>
                  <div>
                    <h4 v-text="item.code" class="ma-0"></h4>
                    <p v-text="item.description" class="ma-0"></p>
                  </div>
                </template>
              </template>
            </v-treeview>
          </v-card-text>
          <v-card-actions>
            <option-buttons
              :edit="true"
              :trash="true"
              name="role"
              :id="role.data._id"
            ></option-buttons>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </user>
</template>

<script>
import Api from './routes/api'
import Role from './models/Role'

export default {
  components: {
    ShowSkeleton: () => import( './cards/ShowSkeleton' )
  },

  computed: {
    activePermissions () { return this.role.data.permissions.map( item => item._id ) }
  },

  data: () => ({
    role: new Role
  }),

  mounted () {
    this.role.getResource( Api.alone( this.$route.params.id ) )
    this.role.load( false )
  }
}
</script>